<template>
  <div class="body">
    <nav v-if="!started">
      <h1>Pomodoro Timer</h1>
    </nav>
    <div v-if="started">{{statement}}</div>
    <div v-if="started" class="timer">
      {{formatTime.m}}
      <span>Min</span>
      {{formatTime.s}}
      <span>Sec</span>
    </div>
    <button v-if="!started" @click="begin">Begin</button>
    <button v-else @click="stop">Stop</button>

    <div v-if="showPopup" class="popup-bg">
      <div class="popup">
        <svg class="close" @click="close" width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd"><path d="M12 0c6.623 0 12 5.377 12 12s-5.377 12-12 12-12-5.377-12-12 5.377-12 12-12zm0 1c6.071 0 11 4.929 11 11s-4.929 11-11 11-11-4.929-11-11 4.929-11 11-11zm0 10.293l5.293-5.293.707.707-5.293 5.293 5.293 5.293-.707.707-5.293-5.293-5.293 5.293-.707-.707 5.293-5.293-5.293-5.293.707-.707 5.293 5.293z"/></svg>
        <h2>Great Work!</h2> 

        <button v-if="!showSummary" @click="showSummary = true">Show Summary</button>
        <button v-else @click="showSummary = false">Hide Summary</button>
        <div class="summary" :class="{shown: showSummary}">
          <div class="row">Rounds of Work: <span class="data">{{rounds}}</span></div>
          <div class="row">Total Study Time: <span class="data time">{{formatSummaryTime.h}} <span>Hr</span> {{formatSummaryTime.m}} <span>Min</span> {{formatSummaryTime.s}} <span>Sec</span></span></div> 
        </div>
      </div>
    </div>
  </div>
</template>

<script>


export default {
  name: "App",
  data(){
    return{
      rounds: 0,
      timer: null,
      elapsedTime: 0,
      remainingTime: 0,
      shortBreak: 300000,
      longBreak: 1800000,
      workTime: 1500000,
      statement: "",
      onBreak: false,
      started: false,
      showPopup: false,
      showSummary: false,
      notifier: null,
      audio: null,
      positive: [
        "Keep up the good work!",
        "You have got this!",
        "There is nothing you can't do!",
        "Make this task your bitch!",
        "Almost there!",
        "Hang in there!",
        "Way to be productive!",
        "Keep it up!",
        "Great Work!"
      ]
    }
  },
  mounted(){
    if (("Notification" in window)) {
        if (Notification.permission !== "denied") {
          Notification.requestPermission()
        }
      }
  },
  computed: {
    formatTime(){
      var m = Math.floor(this.remainingTime / (1000 * 60));
      var s = Math.floor(((this.remainingTime - (m * 1000 * 60))/1000));
      return {m, s}
    },
    formatSummaryTime(){
      var h = Math.floor(this.elapsedTime / (1000 * 60 * 60));
      var m = Math.floor((this.elapsedTime - (h * 1000 * 60 * 60))/ (1000 * 60));
      var s = Math.floor(((this.elapsedTime - (m * 1000 * 60))/1000));
      return {h, m, s}
    }
  },
  methods:{
    close(){
      this.showPopup = false;
      this.showSummary = false;
    },
    begin(){
      this.started = true;
      this.elapsedTime = 0;
      this.rounds = 0;
      this.statement = this.positive[Math.floor(Math.random() * this.positive.length)];
      this.remainingTime = this.workTime;
      var self = this;
      
      if(typeof(Worker) !== "undefined"){
        console.log("Updated")
        this.audio = new Audio("./music/out-of-time-15474.mp3");
        this.audio.loop = true;
        this.timer = new Worker("./js/timer.js")
        
        this.timer.onmessage = function(e){
          self.elapsedTime += e.data;
          self.remainingTime -= e.data;

          //Check if the person is on break
          if(self.onBreak){
            if(self.remainingTime == 0){
              self.statement = self.positive[Math.floor(Math.random() * self.positive.length)];
              self.remainingTime = self.workTime;
              self.onBreak = !self.onBreak;
              self.audio.pause();
              if (("Notification" in window)) {
                // Let's check whether notification permissions have already been granted
                if (Notification.permission === "granted") {
                  // If it's okay let's create a notification
                  new Notification("Time To Get Back To Work!", {vibrate: [200, 100, 200]});
                }
              }
            }
          } else{
            if(self.remainingTime == 0){
              self.onBreak = !self.onBreak;
              self.rounds++;
              self.statement = "Time for a Break!"
              self.audio.play()
              if(self.rounds % 4 == 0 && self.rounds > 0){
                self.remainingTime = self.longBreak;
              } else {
                self.remainingTime = self.shortBreak;
              }
              
              console.log(self.rounds)
              if (("Notification" in window)) {
                // Let's check whether notification permissions have already been granted
                if (Notification.permission === "granted") {
                  // If it's okay let's create a notification
                  new Notification("Time For A Break!");
                }
              }
            }
          }
        }
        // console.log(this.timer)
      }
      
      window.addEventListener("beforeunload", (e) => {
        this.unloadCheck(e);
      })
    },

    unloadCheck(e){
      e.preventDefault();
      e.returnValue = "Are you sure you want to close this tab?";
    },

    stop(){
      this.timer.terminate()
      window.removeEventListener('beforeunload', this.unloadCheck)
      this.started = false;
      this.statement = "";
      this.showPopup = true;
    },
  }
};
</script>

